.header-nav {
    width: 100%;
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-buscador {
    border-radius: 10px;
    width: 30%;
    height: 30%;
    border: 1px solid black;
}

.header-buscador::placeholder {
    padding-left: 5%;
}

.header-buscador:focus {
    outline-color: rgb(212, 89, 147);
}

.header-button {
    border-radius: 25px;
    padding: 0.5%;
    margin-left: 2%;
    cursor: pointer;
    border: 1px solid rgb(212, 89, 147);
    color: rgb(212, 89, 147);
    background-color: white;
}

.header-button:hover {
    border: 1px solid white;
    background-color: rgb(212, 89, 147);
    color: white;
}


@media (max-width: 530px) {
    .header-buscador {
        width: 50%;
        height: 30%;
    }

    .header-button {
        padding: 2.5%;
        margin-left: 6%;
    }
}